function searchForImg(Data, word) {
    const options = {
        id: "logo",
        shouldSort: true,
        threshold: 0,
        location: 0,
        distance: 100,
        maxPatternLength: 32,
        minMatchCharLength: 1,
        keys: [
            "name"
        ]
    };

    const fuse = new Fuse(Data, options);
    const result = fuse.search(word);
    return result;
}

function searchName(Data, word) {
    let options = {
        id: "name",
        shouldSort: true,
        threshold: 0.1,
        location: 0,
        distance: 100,
        maxPatternLength: 32,
        minMatchCharLength: 1,
        keys: [
            "name"
        ]
    };

    let fuse = new Fuse(Data, options);
    let result = fuse.search(word);
    return result;
}

let demo = $('#demo01').animatedModal({
    color: '#ff000'
});

let demo2 = $('#demo02').animatedModal({
    color: '#ff000'
});

function pickRegion() {
    currentTask = 'regions';
    $('#last_name').val('').blur();
    table = '';
    table += tableCreation(regions, table);
    $('#projectsList').html(table);
    demo.open();
}

function updateRecord(div) {
    // statements
    let id = div.id; // or however you want to use the id
    // statements
    let img = searchForImg(data, id);
    let coinHeading = ('<div id="intro"><img src ="' + img + '" id="step1Logo" class="centre" height="50" width="50">');
    coinHeading += ('<h3 id="step1Heading" class="centre">' + id + '</h3>');
    coinHeading += ('</div>');
    let l = ('<div id="step1" class="steps">');
    l += ('<ol class="list">');
    l += ('<li class="item">');
    l += ('<h2 class="headline"><span>Select Your Region</span></h2>' +
        '<div id="pickedRegion" onclick="pickRegion()"></div>' +
        '</li></ol></div>');
    let step1 = coinHeading + l;
    demo.close();
    currentTask = 'regions';
    //enableScroll();
    $('#last_name').val('').blur();
    $('#lastLabel').text(textPicker(inputText));
    $('#main-body').html(step1);
    $('#main-body').css('height', '1000px');
    $('html,body').animate({
        scrollTop: $('#step1Heading').offset().top
    });
   setTimeout(function() {
       table = '';
       table += tableCreation(regions, table);
       allRegions = table;
       $('#projectsList').html(table);
       demo.open();
        //document.getElementById('animatedModal2').style.display = 'unset';
   }, 650);
}

function updateRecord2(div) {
    let id2 = div.id;
    currentTask = 'regions';
    let pickedRegion = searchForImg(regions, id2);
    let name = searchName(regions, id2);
    console.log(name);


    demo.close();
    $('#pickedRegion').html('<img src="' + pickedRegion +'" class="logoScale" height="150" width="150">' +
        '<h class="logoHeading">' + name + '</h>');
}

